.blobs {
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;

  canvas {
    position: absolute;
    top: 0;
    left: 0;
  }

  canvas[resize] {
    width: 100%;
    height: 100%;
  }

  img {
    width: 100vw;
    height: 100vh;
    z-index: 100;
    position: absolute;
  }
}
